import React ,{useState,useEffect} from 'react'
import Base from '../core/Base'
import {Link} from 'react-router-dom'
import { isAutheticated } from '../auth/helper'
import {getProducts, getProduct, deleteProduct} from './helper/adminapicall'

const ManageProducts = () => {
    const[products , setProducts] = useState([])
    const [ele, setEle] = useState(1)
    const {user, token} = isAutheticated();
    const preload  = ()=>{
      getProducts().then(data=>{
        if(data.error){
          console.log(data.error)
        }
        else{
          setProducts(data)
        }
      })
      
    }
    useEffect(()=>{
     preload()
    },[])

    const deleteThisProduct =  productId =>{
      deleteProduct(productId ,user._id,token).then(data=>{
        if(data.error){
          console.log(data.error)
        }
        else{
          preload()
        }

      })

    }

    return (
        <Base title="Welcome Admin" description="Manage Products Here">
        <h3 className="mb-4">Go  To Admin Home!!!</h3>
        <Link className="btn btn-danger" to={`/admin/dashboard`}>
          <span className="">Admin Home</span>
        </Link>
        <div className="row">
          <div className="col-12">
            <h3 className="text-center text-danger my-3">All Products </h3>
            <br/>
           {products.map((product ,index)=>{
             return (
      
              <div key={index} className="row text-center mb-2 ">
              <div className="col-4">
             <h3 className="text-success text-left">{product.name} ({product.updatedAt.slice(0,10)})</h3>
              </div>
              <div className="col-4">
                <Link
                  className="btn btn-success"
                  to={`/admin/product/update/${product._id}`}
                >
                  <span className="">Update</span>
                </Link>
              </div>
              <div className="col-4">
                <button onClick={()=>{
                  deleteThisProduct(product._id)
                }} className="btn btn-danger">
                  Delete 
                </button>
              </div>
              </div>
             )
           })}
          
          </div>
        </div>
      </Base>
    )

}


export default ManageProducts