import React,{useState,useEffect} from "react";
import Base from "../core/Base";
import {Link} from "react-router-dom"
import { isAutheticated } from "../auth/helper";
import { getCategories, deleteCategory,updatetheCategory} from "./helper/adminapicall";

const ManageCategories = () => {

  const[categories , setCategories] = useState([])

  
  const {user, token} = isAutheticated();
  const preload  = ()=>{
    getCategories().then(data=>{
      if(data.error){
        console.log(data.error)
      }
      else{
        setCategories(data)
      }
    })
    
  }
  useEffect(()=>{
   preload()
  },[])

  const deleteThisCategory =  CategoryId =>{
    deleteCategory(CategoryId ,user._id,token).then(data=>{
      if(data.error){
        console.log(data.error)
      }
      else{
        preload()
      }

    })

  }

  return (
    <Base title="Welcome Admin" description="Manage Categories Here">
    <h3 className="mb-4">Go  To Admin Home!!!</h3>
    <Link className="btn btn-danger" to={`/admin/dashboard`}>
      <span className="">Admin Home</span>
    </Link>
    <div className="row">
      <div className="col-12">
        <h3 className="text-center text-danger my-3">Total Categories</h3>
        <br/>
        {
          categories.map((category,index)=>{
           return(
            
            <div key={index} className="row text-center mb-2 ">
            <div className="col-4">
            <h3 className="text-success text-left"> {category.name}</h3>
            </div>
            <div className="col-4">
              <Link
                className="btn btn-success"
                to={`/admin/category/update/${category._id}`}
              >
                <span className="">Update</span>
              </Link>
            </div>
            <div className="col-4">
              <button onClick={()=>{
                 deleteThisCategory(category._id)
              }} className="btn btn-danger">
                Delete 
              </button>
            </div>
            </div>


           )
          })
        }

     </div>
    </div>
  </Base>
  );
};

export default ManageCategories;
